.navigation {
	&__checkbox {
		display: none;
	}

	&__button {
		background-color: $color-white;
		height: 7rem;
		width: 7rem;
		position: fixed;
		top: 6rem;
		right: 6rem;
		border-radius: 50%;
		z-index: 2000;
		box-shadow: 0 1rem 3rem rgba($color-black, 0.1);
		text-align: center;
		cursor: pointer;

		@include responsive(tab-port) {
			top: 4rem;
			right: 4rem;
		}

		@include responsive(phone) {
			top: 3rem;
			right: 3rem;
		}
	}

	&__background {
		height: 6rem;
		width: 6rem;
		border-radius: 50%;
		position: fixed;
		top: 6.5rem;
		right: 6.5rem;
		background-image: radial-gradient(
			$color-primary-light,
			$color-primary-dark
		);
		z-index: 1000;
		_transition: transform 0.8s;
		transition: transform 0.8s cubic-bezier(0.83, 0, 0.17, 1);

		@include responsive(tab-port) {
			top: 4.5rem;
			right: 4.5rem;
		}
		@include responsive(phone) {
			top: 3.5rem;
			right: 3.5rem;
		}
	}

	&__nav {
		height: 100vh;

		position: fixed;
		top: 0;
		right: 0;
		z-index: 1500;
		text-align: center;
		opacity: 0;
		width: 0;

		transition: all 0.8s cubic-bezier(0.68, -0.6, 0.32, 1.6);
	}

	&__list {
		width: 100%;
		list-style: none;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	&__item {
		margin: 1rem;
	}

	&__link {
		&:link,
		&:visited {
			display: inline-block;
			text-decoration: none;
			font-size: 3rem;
			font-weight: 300;
			padding: 1rem 2rem;
			color: $color-white;
			text-transform: uppercase;
			background-image: linear-gradient(
				120deg,
				transparent 0%,
				transparent 50%,
				$color-white 50%
			);
			background-size: 220%;
			transition: all 0.4s;

			span {
				display: inline-block;
				margin-right: 1rem;
			}
		}

		&:hover,
		&:active {
			background-position: 100%;
			color: $color-primary;
			transform: translateX(1rem);
		}
	}

	//FUNCTIONALITY
	&__checkbox:checked ~ &__background {
		transform: scale(100);
	}
	&__checkbox:checked ~ &__nav {
		opacity: 1;
		width: 100%;
	}

	&__icon {
		position: relative;
		margin-top: 3.5rem;

		&,
		&::after,
		&::before {
			width: 3rem;
			height: 2px;
			background-color: $color-grey-dark-3;
			display: inline-block;
		}

		&::before,
		&::after {
			content: '';
			position: absolute;
			left: 0;
			transition: all 0.2s;
		}

		&::before {
			top: -0.8rem;
		}
		&::after {
			top: 0.8rem;
		}
	}

	&__button:hover &__icon::before {
		top: -1rem;
	}

	&__button:hover &__icon::after {
		top: 1rem;
	}

	&__checkbox:checked + &__button &__icon {
		background-color: transparent;
	}

	&__checkbox:checked + &__button &__icon::before {
		top: 0;
		transform: rotate(135deg);
	}
	&__checkbox:checked + &__button &__icon::after {
		top: 0;
		transform: rotate(-135deg);
	}
}
