.form {
	&__group {
		margin-bottom: 2rem;
		// &:not(:last-child) {
		//     margin-bottom: 2rem;
		// }
	}

	&__input {
		font-family: inherit;
		font-size: 1.5rem;
		color: inherit;
		padding: 1.5rem 2rem;
		border-radius: 2px;
		background-color: rgba($color-white, 0.5);
		border: none;
		border-bottom: 3px solid transparent;
		width: 90%;
		display: block;
		transition: all 0.3s;

		@include responsive(tab-port) {
			width: 100%;
		}

		&:focus {
			outline: none;
			box-shadow: 0 1rem 2rem rgba($color-black, 0.1);
			border-bottom: 3px solid $color-primary;
		}

		&:focus:invalid {
			border-bottom: 3px solid $color-secondary-dark;
		}

		&::-webkit-input-placeholder {
			color: $color-grey-dark-2;
		}
	}

	&__label {
		font-size: 1.2rem;
		font-weight: bold;
		margin-left: 2rem;
		margin-top: 0.7rem;
		display: block;
		transition: all 0.3s;
	}

	&__input:placeholder-shown + &__label {
		opacity: 0;
		visibility: hidden;
		transform: translateY(-4rem);
	}

	&__radio-input {
		display: none;
	}

	&__radio-group {
		width: 49%;
		display: inline-block;

		@include responsive(tab-port) {
			width: 100%;
			margin-bottom: 2rem;
		}
	}

	&__radio-label {
		font-size: $default-font-size;
		cursor: pointer;
		position: relative;
		padding-left: 4rem;
	}

	&__radio-button {
		height: 3rem;
		width: 3rem;
		border: 5px solid $color-primary;
		display: inline-block;
		border-radius: 50%;
		position: absolute;
		top: -0.5rem;
		left: 0;

		&::after {
			content: '';
			height: 1.3rem;
			width: 1.3rem;
			display: block;
			border-radius: 50%;
			border: 5px solid $color-primary;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: $color-primary;
			opacity: 0;
			transition: opacity 0.2s;
		}
	}

	&__radio-input:checked ~ &__radio-label &__radio-button::after {
		opacity: 1;
	}
}
