.popup {
	height: 100vh;
	width: 100%;
	position: fixed;
	top: 0;
	left: 0;
	background-color: rgba($color-black, 0.8);
	z-index: 9999;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s;

	&__content {
		@include absoluteCenter;
		width: 75%;
		height: 50rem;
		background-color: $color-white;
		box-shadow: 0 2rem 4rem rgba($color-black, 0.2);
		border-radius: 3px;
		display: table;
		overflow: hidden;
		opacity: 0;
		transform: translate(-50%, -50%) scale(0.25);
		transition: all 0.5s 0.2s;
	}

	&__left {
		width: 33.33%;
		_float: left;
		display: table-cell;
	}

	&__right {
		width: 66.66%;
		display: table-cell;
		vertical-align: middle;
		padding: 3rem 5rem;
	}

	&__img {
		display: block;
		width: 100%;
	}

	&__text {
		font-size: 1.4rem;
		margin-bottom: 4rem;

		-moz-column-count: 2;
		-moz-column-gap: 4rem;
		-moz-column-rule: 1px solid $color-light-grey-1;

		column-count: 2;
		column-gap: 4rem;
		column-rule: 1px solid $color-light-grey-1;

		-moz-hyphens: auto;
		-webkit-hyphens: auto;
		-ms-hyphens: auto;
		hyphens: auto;
	}

	&:target {
		opacity: 1;
		visibility: visible;
	}

	&:target &__content {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}

	&__close {
		&:link,
		&:visited {
			color: $color-grey-dark;
			position: absolute;
			top: 2.5rem;
			right: 2.5rem;
			font-size: 3rem;
			text-decoration: none;
			display: inline-block;
			transition: all 0.2s;
			line-height: 1;
		}

		&:hover {
			color: $color-primary;
		}
	}

	@include responsive(tab-port) {
		&__content {
			@include absoluteCenter;
			width: 75%;
			height: 50rem;
			background-color: $color-white;
			box-shadow: 0 2rem 4rem rgba($color-black, 0.2);
			border-radius: 3px;
			display: flex;
			flex-direction: column;
			overflow: hidden;
			opacity: 0;
			transform: translate(-50%, -50%) scale(0.25);
			transition: all 0.5s 0.2s;
		}

		&__left {
			width: 50%;
			_float: left;
			display: none;
		}

		&__right {
			width: 100%;
			_display: table-cell;
			vertical-align: auto;
			padding: 3rem 5rem;
		}

		&__text {
			-moz-column-count: 1;
			column-count: 1;
		}
	}
}
