.section-about {
	background-color: $color-light-grey-1;
	padding: 20rem 0;
	margin-top: -20vh;

	@include responsive(tab-port) {
		padding: 20rem 0;
	}
}

.section-features {
	padding: 20rem 0;
	background-image: linear-gradient(
			to right bottom,
			rgba($color-primary-light, 0.8),
			rgba($color-primary-dark, 0.8)
		),
		url('/assets/img/nat-4.jpg');
	background-size: cover;
	background-position: top;
	transform: skewY(-7deg);
	margin-top: -12rem;

	& > * {
		transform: skewY(7deg);
	}

	@include responsive(tab-port) {
		padding: 10rem 0;
	}
}

.section-tours {
	background-color: $color-light-grey-1;
	padding: 20rem 0 15rem 0;
	margin-top: -10rem;

	@include responsive(tab-port) {
		_padding: 15rem 0 10rem 0;
	}
}

.section-stories {
	padding: 15rem 0;
	_background-color: $color-light-grey-1;
	position: relative;

	@include responsive(tab-port) {
		padding: 10rem 0;
	}
}

.section-book {
	padding: 15rem 0;
	background-image: linear-gradient(
		to right bottom,
		$color-primary-light,
		$color-primary-dark
	);

	@include responsive(tab-port) {
		padding: 10rem 0;
	}
}

.book {
	background-image: linear-gradient(
			105deg,
			rgba($color-white, 0.9) 0%,
			rgba($color-white, 0.9) 50%,
			transparent 50%
		),
		url('/assets/img/nat-10.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	border-radius: 3px;
	box-shadow: 0 1.5rem 4rem rgba($color-black, 0.2);

	height: 50rem;
	overflow: hidden;

	@include responsive(tab-land) {
		background-image: linear-gradient(
				105deg,
				rgba($color-white, 0.9) 0%,
				rgba($color-white, 0.9) 65%,
				transparent 65%
			),
			url('/assets/img/nat-10.jpg');
	}

	@include responsive(tab-port) {
		background-image: linear-gradient(
				to right,
				rgba($color-white, 0.7) 0%,
				rgba($color-white, 0.7) 100%
			),
			url('/assets/img/nat-10.jpg');
		height: 60rem;
	}

	&__form {
		width: 50%;
		padding: 6rem;

		@include responsive(tab-land) {
			width: 65%;
		}

		@include responsive(tab-port) {
			width: 100%;
		}
	}
}
