body {
	font-family: 'Lato', sans-serif;
	font-weight: 400;
	line-height: 1.7;
	color: $color-grey-dark;
}

h1 {
	font-size: 2rem;

	@media #{$mq-1000-up} {
		font-size: 3rem;
	}
}

h2 {
	font-size: 1.5rem;

	@media #{$mq-1000-up} {
		font-size: 2rem;
	}
}

h3 {
	font-size: 1.25rem;

	@media #{$mq-1000-up} {
		font-size: 1.5rem;
	}
}

.heading-primary {
	color: $color-white;
	text-transform: uppercase;
	backface-visibility: hidden;
	margin-bottom: 6rem;

	&--main {
		display: block;
		font-size: 6rem;
		font-weight: 400;
		letter-spacing: 3.5rem;
		animation: moveInLeft 1s ease-out;

		@include responsive(phone) {
			letter-spacing: 1rem;
			font-size: 4rem;
		}
	}
	&--sub {
		display: block;
		font-size: 2rem;
		font-weight: 700;
		letter-spacing: 1.74rem;
		animation: moveInRight 1s ease-out;

		@include responsive(phone) {
			letter-spacing: 0.5rem;
		}
	}

	@include responsive(phone) {
		margin-bottom: 4rem;
	}
}

.heading-secondary {
	font-size: 3.2rem;
	text-transform: uppercase;
	font-weight: 700;
	display: inline-block;
	background-image: linear-gradient(
		to right,
		$color-primary-light,
		$color-primary-dark
	);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;
	letter-spacing: 2px;
	transition: all 0.2s;

	@include responsive(tab-port) {
		font-size: 3rem;
	}

	@include responsive(phone) {
		font-size: 2.5rem;
	}

	&:hover {
		transform: skewY(2deg) skewX(15deg) scale(1.1);
		text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
	}
}

.heading-tertiary {
	font-size: $default-font-size;
	font-weight: 700;
	text-transform: uppercase;
}

.paragraph {
	font-size: $default-font-size;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}
